$navColor: transparent;
$logoColor: black;
$logoColor_hover: white;
$navItemColor: white;
$navItemColor_hover: black;
$burgerColor: black;
$MQ_menuColor: rgba(36, 47, 49, 0.465);
.header {
  width: 100%;
  height: auto;
  &-promo {
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.nav {
  display: flex;
  justify-content: space-around;
  width: 100%;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background-color: $navColor;
  z-index: 1000;
  margin: 0 auto;
  //box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.5);
}

.logo {
  text-decoration: none;
  font-size: 40px;
  color: $logoColor;
  z-index: 99;
  align-items: center;
  transition: color 0.25s ease;
  @media screen and (max-width: 550px) {
    font-size: 30px;
  }
  &:hover {
    text-shadow: 0px 1px 1px black;
    color: $logoColor_hover;
  }
}

.navItems {
  display: flex;
  list-style: none;
  position: relative;
  padding: 0;
  &__item {
    &:not(:first-child) {
      margin-left: 30px;
    }
    &-inner {
      text-decoration: none;
      font-size: 25px;
      color: $navItemColor;
      transition: all 0.3s ease;
      text-shadow: 0px 1px 1px black;
      &:hover {
        color: $navItemColor_hover;
        transform: scale(1.07);
      }
    }
  }
}

.scrollLock {
  overflow: hidden;
}

.burger {
  display: block;
  position: relative;
  width: 35px;
  height: 25px;
  z-index: 999;
  margin: 0;
  cursor: pointer;
}

.activeB:before {
  transform: rotate(45deg);
  top: 12px !important;
}

.activeB:after {
  transform: rotate(-45deg);
  bottom: 10px !important;
}

.activeB > span {
  transform: scale(0);
}

.burger:before,
.burger:after,
.burger > span {
  content: "";
  background-color: $burgerColor;
  position: absolute;
  width: 100%;
  height: 3px;
  z-index: 99;
  left: 0;
  border-radius: 100px;
}

.burger > span {
  top: 11px;
  transition: all 0.2s ease;
}

.burger:before {
  top: 0;
  transition: all 0.35s ease;
}

.burger:after {
  bottom: 0;
  transition: all 0.35s ease;
}

.logoCont {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $navColor;
  padding: 7px 15px;
  z-index: 98;
  width: 100%;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}

.inner {
  position: relative;
  max-width: 1140px;
  width: 100%;
  cursor: default;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: flex-end;
}

.navCont {
  position: fixed;
  top: 0%;
  right: -100%;
  width: 100%;
  height: 100%;
  background: $MQ_menuColor;
  padding-top: 40px;
  z-index: 10;
  display: flex;
  justify-content: end;
  transition: all 0.6s ease;
  overflow: auto;
  z-index: 9;
}

.active {
  right: 0%;
  padding-right: 10px;
}

.navItems {
  display: flex;
  flex-direction: column;
  margin: 0;
  align-items: flex-end;
  position: absolute;
  &__item {
    margin-left: 0 !important;
    margin-top: 40px;
    transition: all 0.3s ease;
    &:hover {
      //position: relative;
      // top: -3px;
      // transform: scale(1.05);
    }
    &:last-child {
      margin-bottom: 40px;
    }
    &:first-child {
      margin-top: 45px;
    }
    &-inner {
      font-size: 28px;
    }
  }
}

.img {
  max-width: 100vw;
  width: 100%;
  position: absolute;
  z-index: -1;
  object-fit: cover;
  &-promo {
    top: 0;
    height: 100vh;
    left: 0;
    background-image: url("../img/header-img.jpg");
    //  background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
  }
  &-rel {
    position: relative;
    max-width: 530px;
    z-index: 1;
    max-height: 400px;
    width: 100vw;
    height: 100vh;
    @media screen and (max-width: 600px) {
      width: 400px;
      height: 100vh;
    }
    @media screen and (max-width: 450px) {
      width: 290px;
      height: 100vh;
    }
  }
  &-logo {
    position: relative;
    max-width: 300px;
    z-index: 1;
    max-height: 240px;
    width: 100vw;
    height: 100vh;
    object-fit: contain;
  }
  &-count {
    max-height: 600px;
    position: absolute;
    background-image: url("../img/info-img1.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 600px;
    left: 0;
    object-fit: cover;
    @media screen and (max-width: 940px) {
      height: 500px;
    }
  }
  &-count2 {
    max-height: 600px;
    position: absolute;
    background-image: url("../img/footer-img.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 600px;
    left: 0;
    object-fit: cover;
    @media screen and (max-width: 940px) {
      height: 500px;
    }
  }
  &-count3 {
    max-height: 600px;
    position: absolute;
    background-image: url("../img/info-img3.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 600px;
    left: 0;
    object-fit: cover;
    @media screen and (max-width: 940px) {
      height: 500px;
    }
  }
}

.textCont {
  max-width: 1140px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 0;
  margin-bottom: 50px;
}

.text {
  font-size: 18px;
  color: white;
  text-shadow: 0px 1px 1px black;
  text-align: center;
  max-width: 70%;
  margin: 0 auto;
  @media screen and (max-width: 400px) {
    font-size: 13.5px;
  }
  &-promo {
    font-size: 40px;
  }
  &-centred {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 10px;
    display: flex;
    justify-content: center;
  }
  &-black {
    color: #000;
  }
  &-bold {
    font-size: 20px !important;
  }
  &-heading {
    font-size: 80px;
    margin-bottom: 30px;
  }
  @media screen and (max-width: 800px) {
    max-width: 85%;
    font-size: 16px;
    &-promo {
      font-size: 30px;
    }
    &-heading {
      font-size: 55px;
    }
  }
  @media screen and (max-width: 550px) {
    font-size: 15px;
    &-promo {
      font-size: 25px;
    }
    &-heading {
      font-size: 44px;
    }
  }
  @media screen and (max-width: 400px) {
    &-promo {
      font-size: 20px;
    }
    &-heading {
      font-size: 35px;
    }
  }
}

.btn {
  padding: 10px 20px;
  font-size: 20px;
  background-color: rgb(238, 86, 86);
  box-shadow: 0px 0px 5px 0px rgb(71, 71, 71);
  border: none;
  cursor: pointer;
  margin: 5px;
  transition: all 0.3s ease;
  text-align: center;
  &:hover {
    background-color: rgb(219, 53, 53);
    transform: scale(1.1);
  }
  &-counts {
    margin-left: 30px;
    margin-top: 30px;
    max-width: 180px;
  }
  &-blue {
    background-color: rgb(86, 157, 238);
    &:hover {
      background-color: rgb(50, 131, 224);
      transform: scale(1.1);
    }
  }
  @media screen and (max-width: 550px) {
    font-size: 16px;
  }
}

.navColored {
  &:before {
    content: "";
    width: 100vw;
    position: absolute;
    background-color: rgba(68, 105, 126, 0.95);
    height: 100%;
    top: 0;
    left: 0;
    animation: navColored 0.3s ease;
  }
}

@keyframes navColored {
  from {
    background-color: rgba(33, 97, 134, 0);
    top: -50px;
  }
  to {
    background-color: rgba(68, 105, 126, 0.95);
    top: 0px;
  }
}
