html {
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}

pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}

button,
input {
  /* 1 */
  overflow: visible;
}

button,
select {
  /* 1 */
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  padding: 0;
  /* 3 */
  white-space: normal;
  /* 1 */
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.header {
  width: 100%;
  height: auto;
}

.header-promo {
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav {
  display: flex;
  justify-content: space-around;
  width: 100%;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background-color: transparent;
  z-index: 1000;
  margin: 0 auto;
}

.logo {
  text-decoration: none;
  font-size: 40px;
  color: black;
  z-index: 99;
  align-items: center;
  transition: color 0.25s ease;
}

.logo:hover {
  text-shadow: 0px 1px 1px black;
  color: white;
}

.navItems {
  display: flex;
  list-style: none;
  position: relative;
  padding: 0;
}

.navItems__item:not(:first-child) {
  margin-left: 30px;
}

.navItems__item-inner {
  text-decoration: none;
  font-size: 25px;
  color: white;
  transition: all 0.3s ease;
  text-shadow: 0px 1px 1px black;
}

.navItems__item-inner:hover {
  color: black;
  transform: scale(1.07);
}

.scrollLock {
  overflow: hidden;
}

.burger {
  display: block;
  position: relative;
  width: 35px;
  height: 25px;
  z-index: 999;
  margin: 0;
  cursor: pointer;
}

.activeB:before {
  transform: rotate(45deg);
  top: 12px !important;
}

.activeB:after {
  transform: rotate(-45deg);
  bottom: 10px !important;
}

.activeB > span {
  transform: scale(0);
}

.burger:before,
.burger:after,
.burger > span {
  content: "";
  background-color: black;
  position: absolute;
  width: 100%;
  height: 3px;
  z-index: 99;
  left: 0;
  border-radius: 100px;
}

.burger > span {
  top: 11px;
  transition: all 0.2s ease;
}

.burger:before {
  top: 0;
  transition: all 0.35s ease;
}

.burger:after {
  bottom: 0;
  transition: all 0.35s ease;
}

.logoCont {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
  padding: 7px 15px;
  z-index: 98;
  width: 100%;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}

.inner {
  position: relative;
  max-width: 1140px;
  width: 100%;
  cursor: default;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: flex-end;
}

.navCont {
  position: fixed;
  top: 0%;
  right: -100%;
  width: 100%;
  height: 100%;
  background: rgba(36, 47, 49, 0.465);
  padding-top: 40px;
  z-index: 10;
  display: flex;
  justify-content: end;
  transition: all 0.6s ease;
  overflow: auto;
  z-index: 9;
}

.active {
  right: 0%;
  padding-right: 10px;
}

.navItems {
  display: flex;
  flex-direction: column;
  margin: 0;
  align-items: flex-end;
  position: absolute;
}

.navItems__item {
  margin-left: 0 !important;
  margin-top: 40px;
  transition: all 0.3s ease;
}

.navItems__item:last-child {
  margin-bottom: 40px;
}

.navItems__item:first-child {
  margin-top: 45px;
}

.navItems__item-inner {
  font-size: 28px;
}

.img {
  max-width: 100vw;
  width: 100%;
  position: absolute;
  z-index: -1;
  object-fit: cover;
}

.img-promo {
  top: 0;
  height: 100vh;
  left: 0;
  background-image: url("../img/header-img.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
}

.img-rel {
  position: relative;
  max-width: 530px;
  z-index: 1;
  max-height: 400px;
  width: 100vw;
  height: 100vh;
}

.img-logo {
  position: relative;
  max-width: 300px;
  z-index: 1;
  max-height: 240px;
  width: 100vw;
  height: 100vh;
  object-fit: contain;
}

.img-count {
  max-height: 600px;
  position: absolute;
  background-image: url("../img/info-img1.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 600px;
  left: 0;
  object-fit: cover;
}

.img-count2 {
  max-height: 600px;
  position: absolute;
  background-image: url("../img/footer-img.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 600px;
  left: 0;
  object-fit: cover;
}

.img-count3 {
  max-height: 600px;
  position: absolute;
  background-image: url("../img/info-img3.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 600px;
  left: 0;
  object-fit: cover;
}

.textCont {
  max-width: 1140px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 0;
  margin-bottom: 50px;
}

.text {
  font-size: 18px;
  color: white;
  text-shadow: 0px 1px 1px black;
  text-align: center;
  max-width: 70%;
  margin: 0 auto;
}

.text-promo {
  font-size: 40px;
}

.text-centred {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px;
  display: flex;
  justify-content: center;
}

.text-black {
  color: #000;
}

.text-bold {
  font-size: 20px !important;
}

.text-heading {
  font-size: 80px;
  margin-bottom: 30px;
}

.btn {
  padding: 10px 20px;
  font-size: 20px;
  background-color: #ee5656;
  box-shadow: 0px 0px 5px 0px #474747;
  border: none;
  cursor: pointer;
  margin: 5px;
  transition: all 0.3s ease;
  text-align: center;
}

.btn:hover {
  background-color: #db3535;
  transform: scale(1.1);
}

.btn-counts {
  margin-left: 30px;
  margin-top: 30px;
  max-width: 180px;
}

.btn-blue {
  background-color: #569dee;
}

.btn-blue:hover {
  background-color: #3283e0;
  transform: scale(1.1);
}

.navColored:before {
  content: "";
  width: 100vw;
  position: absolute;
  background-color: rgba(68, 105, 126, 0.95);
  height: 100%;
  top: 0;
  left: 0;
  animation: navColored 0.3s ease;
}

@keyframes navColored {
  from {
    background-color: rgba(33, 97, 134, 0);
    top: -50px;
  }

  to {
    background-color: rgba(68, 105, 126, 0.95);
    top: 0px;
  }
}

* {
  box-sizing: border-box;
  outline: none;
  font-family: "Girassol", cursive;
}

.advCont {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  max-width: 1140px;
  position: relative;
  margin: 40px auto;
}

.advCont .advItem {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.33333% - 30px);
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 15px auto 35px;
}

.advCont .advItem__text {
  margin-top: 15px;
}

.fa {
  font-size: 35px;
  margin-bottom: 10px;
}

.countsCont {
  position: relative;
  width: 100vw;
  top: 0;
  max-width: 1140px;
  margin: 0 auto;
}

.counts {
  position: relative;
  width: 60%;
  max-width: 800px;
  margin-left: 8vw;
  height: 600px;
  background-color: rgba(26, 85, 119, 0.609);
  left: -10vw;
}

.counts__text {
  text-align: start;
  margin: 0;
  margin-left: 30px;
  margin-top: 40px;
  max-width: 500px;
}

.nums {
  display: flex;
  justify-content: center;
}

.numsBlock {
  margin: 15px;
  padding: 10px;
  max-width: 200px;
  border: 2px solid white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.numsBlock__text {
  text-align: center;
  width: 100%;
  margin: 10px;
  padding: 0;
}

.numsBlock__num {
  text-align: center;
  max-width: 120px;
  width: 100vw;
  margin: 10px;
  padding: 0 0 20px;
  font-size: 40px;
  color: white;
}

.infoCont {
  display: flex;
  max-width: 1140px;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
}

.cover {
  padding: 100px 40px;
  background-color: rgba(8, 58, 87, 0.746);
  margin-left: 20px;
}

.logos {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  max-width: 1140px;
  position: relative;
  margin: 40px auto;
}

.img-logo {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.33333% - 30px);
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0px auto 0px;
}

.form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 100vw;
}

.form__btn {
  padding: 20px 30px;
  max-width: 200px;
  text-align: center;
  width: 200px;
}

.form__text {
  margin-left: 0;
}

.input {
  margin: 30px;
  font-size: 40px;
  border: none;
  text-align: center;
  padding: 10px;
  max-width: 95vw;
}

.socials {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logoCont-footer {
  max-width: 1140px;
  margin: 20px auto;
}

.social {
  color: black;
  max-height: 28px;
}

.social:not(:first-child) {
  margin-left: 10px;
}

.social .fa {
  font-size: 28px;
}

.social:hover {
  color: rgba(8, 58, 87, 0.746);
}

body {
  overflow-x: scroll;
}

@media screen and (max-width: 960px) {
  .advCont {
    justify-content: center;
  }

  .advCont .advItem {
    width: calc(50% - 30px);
  }

  .logos {
    justify-content: center;
  }

  .img-logo {
    width: calc(50% - 30px);
  }
}

@media screen and (max-width: 950px) {
  .infoCont {
    flex-direction: column;
  }

  .cover {
    margin-left: 0;
    padding: 20px;
  }
}

@media screen and (max-width: 940px) {
  .img-count {
    height: 500px;
  }

  .img-count2 {
    height: 500px;
  }

  .img-count3 {
    height: 500px;
  }

  .counts {
    flex-direction: column;
    max-width: 100vw;
    width: 100%;
    left: 0vw;
    margin-left: 0;
    height: 500px;
    display: flex;
    align-items: center;
  }
}

@media screen and (max-width: 800px) {
  .text {
    max-width: 85%;
    font-size: 16px;
  }

  .text-promo {
    font-size: 30px;
  }

  .text-heading {
    font-size: 55px;
  }
}

@media screen and (max-width: 600px) {
  .img-rel {
    width: 400px;
    height: 100vh;
  }
}

@media screen and (max-width: 560px) {
  .advCont .advItem {
    width: calc(83.33333% - 30px);
  }

  .img-logo {
    width: calc(83.33333% - 30px);
  }
}

@media screen and (max-width: 550px) {
  .logo {
    font-size: 30px;
  }

  .text {
    font-size: 15px;
  }

  .text-promo {
    font-size: 25px;
  }

  .text-heading {
    font-size: 44px;
  }

  .btn {
    font-size: 16px;
  }
}

@media screen and (max-width: 450px) {
  .img-rel {
    width: 290px;
    height: 100vh;
  }

  .cover {
    padding: 10px;
  }
}

@media screen and (max-width: 400px) {
  .text {
    font-size: 13.5px;
  }

  .text-promo {
    font-size: 20px;
  }

  .text-heading {
    font-size: 35px;
  }

  .socials {
    margin-top: 20px;
  }

  .logoCont-footer {
    flex-direction: column;
  }
}

@media screen and (max-width: 390px) {
  .numsBlock {
    padding: 0;
    margin: 7px;
  }
}

.webp .img-promo { background-image: url(../img/header-img.webp); }

.webp .img-count { background-image: url(../img/info-img1.webp); }

.webp .img-count2 { background-image: url(../img/footer-img.webp); }

.webp .img-count3 { background-image: url(../img/info-img3.webp); }