@import "./helpers/fonts.scss";
@import "./helpers/normalize.scss";
@import "./helpers/smart-grid.scss";

@import "../modules/_header/header.scss";

@import "../modules/_footer/footer.scss";

* {
  box-sizing: border-box;
  outline: none;
  font-family: "Girassol", cursive;
}

.advCont {
  @include row-flex();
  @include md(justify-content, center);
  max-width: 1140px;
  position: relative;

  margin: 40px auto;

  .advItem {
    @include col();
    @include size(4);
    @include size-md(6);
    @include size-xs(10);
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 15px auto 35px;

    &__text {
      margin-top: 15px;
    }
  }
}

.fa {
  font-size: 35px;
  margin-bottom: 10px;
}

.countsCont {
  position: relative;
  width: 100vw;
  top: 0;
  max-width: 1140px;
  margin: 0 auto;
}

.counts {
  position: relative;
  width: 60%;
  max-width: 800px;
  margin-left: 8vw;

  height: 600px;
  background-color: rgba(26, 85, 119, 0.609);
  left: -10vw;

  @media screen and (max-width: 940px) {
    flex-direction: column;
    max-width: 100vw;
    width: 100%;
    left: 0vw;
    margin-left: 0;
    height: 500px;
    display: flex;
    align-items: center;
  }
  &__text {
    text-align: start;
    margin: 0;
    margin-left: 30px;
    margin-top: 40px;
    max-width: 500px;
  }
}

.nums {
  display: flex;
  justify-content: center;

  &Block {
    margin: 15px;
    padding: 10px;
    max-width: 200px;
    border: 2px solid white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 30px;

    @media screen and (max-width: 390px) {
      padding: 0;
      margin: 7px;
    }

    &__text {
      text-align: center;
      width: 100%;
      margin: 10px;
      padding: 0;
    }
    &__num {
      text-align: center;
      max-width: 120px;
      width: 100vw;

      margin: 10px;
      padding: 0 0 20px;
      font-size: 40px;
      color: white;

      @media screen and (max-width: 940px) {
        // height: 500px;
      }
    }
  }
}

.infoCont {
  display: flex;
  max-width: 1140px;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  @media screen and (max-width: 950px) {
    flex-direction: column;
  }
}

.cover {
  padding: 100px 40px;
  background-color: rgba(8, 58, 87, 0.746);
  margin-left: 20px;

  @media screen and (max-width: 950px) {
    margin-left: 0;
    padding: 20px;
  }

  @media screen and (max-width: 450px) {
    padding: 10px;
  }
}

.logos {
  @include row-flex();
  @include md(justify-content, center);
  max-width: 1140px;
  position: relative;

  margin: 40px auto;
}

.img-logo {
  @include col();
  @include size(4);
  @include size-md(6);
  @include size-xs(10);
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0px auto 0px;
}

.form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 100vw;

  &__btn {
    padding: 20px 30px;
    max-width: 200px;
    text-align: center;
    width: 200px;
  }

  &__text {
    margin-left: 0;
  }
}

.input {
  margin: 30px;
  font-size: 40px;
  border: none;
  text-align: center;
  padding: 10px;
  max-width: 95vw;
}

.socials {
  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (max-width: 400px) {
    margin-top: 20px;
  }
}

.logoCont-footer {
  max-width: 1140px;
  margin: 20px auto;

  @media screen and (max-width: 400px) {
    flex-direction: column;
  }
}

.social {
  color: black;
  max-height: 28px;

  &:not(:first-child) {
    margin-left: 10px;
  }

  .fa {
    font-size: 28px;
  }

  &:hover {
    color: rgba(8, 58, 87, 0.746);
  }
}

body {
  overflow-x: scroll;
}
